.search-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-inputs {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
}

.search-inputs label,
.search-inputs input,
.search-inputs select {
    padding: 8px;
}

